import {MOCADI_Matter} from "../vendor/mocadimatter.js";
import {ElementDensity} from "../vendor/elements_density";
import {ElementMass} from "../vendor/elements_mass";
import {styles} from "./styles.js";
import {withAttr_nr, text_error, enterescape, escape,debounce,button, button_route, svg_delete, svg_edit, svg_down, svg_up, svg_info} from "./utils.js";
import {ToggleComponent} from "./components.js";
import {FileLoader, SaveFile, noredraw} from "./utils.js";
import {Catima} from "./catima.js";
import {App} from "./app.js";
import {NucData} from "../vendor/nucdata";
import {NucID} from "../vendor/elements";
import {spline} from "../vendor/spline";

function GetDensity(z){
    if(z>0 && z<99)return ElementDensity.GetDensity(z);
    if(z>200){
        var r = MOCADI_Matter.Get(z);
        if(r.density)
            return r.density;
        }
    return 0.0;
}

function CompundMatterSelect(args){
    var options = [];
    
    for(var entry in MOCADI_Matter.map){
      var name;
      name = MOCADI_Matter.GetName(entry);
      options.push([entry, name]);
    }
    
    options.sort(function(a,b){return a[1].localeCompare(b[1]);});
    
    return m("select",{id:"id_compound_select",class:"db w-100",onchange:m.withAttr("value",args.onchange)},
            m("option",{value:""},"Add Compound"), // default 1st value
            options.map(function(item){
                return m("option",{value:item[0]},item[1]);
            }));
}

var render_composition = function(data){
    var components = [];
    if(data.hasOwnProperty('c')){
        for(var key in data['c']){
            if(data['c'].hasOwnProperty(key)){
                components.push([key,data['c'][key]]);
            }
        }
    }
    if(components.length){
        return m("table",{class:styles.table},[
                    m("thead",{},
                        m("tr",{class:styles.thead_tr},
                            [m("th",{class:styles.th},"Element"),
                             m("th",{class:styles.th},"Weight Fraction")])
                    ),
                    m("tbody",{},[
         components.map(function(item){
            return m("tr",{class:styles.tr},[
                        m("td",{class:styles.td},item[0]),
                        m("td",{class:styles.td},item[1])
                    ]);
            //return item[0] + ": "+item[1];
         })   
        ])]);
    }
    return;
  };

export var MatterLayer = function(data){
  this.name = data.name||"";
  this.symbol = data.symbol||"";
  this.A = data.A||"";
  this.mass = data.mass||"";
  this.Z = data.Z||"";
  this.rho = data.rho||0;
  this.th = data.th||0;
  this.gas = data.gas||false;
  this.l = ""||0;
  this.I = data.I || 0;
  
  if(data.l){
      this.l = data.l;
  }
  else{
    var rho = parseFloat(data.rho);
    if(!isNaN(rho) && rho >0)
    {
      this.l = (((parseFloat(data.th)/rho)/1000).toFixed(4));
      }
    }
    
  // if mass is not provided take some default  
  if(!data.mass){
      if(this.A === ""){
        this.mass = (ElementMass.GetMass(this.Z).toPrecision(5)||"");
      }
      else{
        this.mass = (NucData.GetNucData(parseInt(data.A),parseInt(data.Z))["M"]||"");
      }
  }

  if(this.symbol.length==0){
      if(this.Z<120){
        this.symbol = NucID.GetSymbol(this.A,this.Z);
      }
      else{
        var r = MOCADI_Matter.Get(this.Z);
        this.symbol = (r.key)?r.key:"";
        this.mass = "";
        this.A = "";
      }
  }
  
};

MatterLayer.prototype.toArray = function(){
    var z = parseInt(this.Z);
    if(isNaN(z)){
        return [];
    }    
    var density = parseFloat(this.rho);
    if(isNaN(density)){
        return [];
    }    
    var Th = parseFloat(this.th);
    if(isNaN(Th)){
        return [];
    }
    var Mass;
    if(this.mass === "" || this.mass===null){
        Mass=0;
    }
    else{
        Mass = parseFloat(this.mass);
        if(isNaN(Mass) || Mass<0){
            return [];
        }
    }
    var a;
    if(this.A === "" || this.A===null){
        a=0;
    }
    else{
        a = parseFloat(this.A);
        if(isNaN(a) || a<0){
            a = 0;
        }
    }
    var ipot = 0.0;
    if(this.I !== "" && this.I !== null){
        var v = parseFloat(this.I);
        if( !isNaN(v) && v>0.0){
            ipot = v;
            }
        }

    var gas = (this.gas)?1:0;
    return [a,z,density,Th,gas,Mass,ipot];
};

MatterLayer.prototype.toObject = function(){
    var z = parseInt(this.Z);
    if(isNaN(z)){
        return {};
    }    
    var density = parseFloat(this.rho);
    if(isNaN(density)){
        return {};
    }    
    var Th = parseFloat(this.th);
    if(isNaN(Th)){
        return {};
    }
    var Mass;
    if(this.mass === "" || this.mass===null){
        Mass=0;
    }
    else{
        Mass = parseFloat(this.mass);
        if(isNaN(Mass) || Mass<0){
            return {};
        }
    }
    var a;
    if(this.A === "" || this.A===null){
        a=0;
    }
    else{
        a = parseFloat(this.A);
        if(isNaN(a) || a<0){
            a = 0;
        }
    }
    var ipot = 0.0;
    if(this.I !== "" && this.I !== null){
        var v = parseFloat(this.I);
        if( !isNaN(v) && v>0.0){
            ipot = v;
            }
        }

    var gas = (this.gas)?1:0;
    return {
        "A":a,
        "Z":z,
        "rho":density,
        "th":Th,
        "mass":Mass,
        "gas":gas,
        "I":ipot,
        "name":this.name
    };
};



// Main Matter Editor Component ///
var MatterEditor = {};

MatterEditor.vm = {
  layers : [],
  editindex: -1,
  thickness:0,
  length:0,
  calculate:function(){
      this.thickness = 0;
      this.length = 0;
      for(var i=0;i<this.layers.length;i++){
          var e = this.layers[i];
          this.thickness =  this.thickness + parseFloat(e.th);
          this.length =  this.length + parseFloat(e.l);
      }
  }
};

MatterEditor.ui = {};
MatterEditor.ui.changeSymbol = function(index, val){
    var item = MatterEditor.vm.layers[index];
    // std element
    var res = NucID.GetNuc(val);
    if(res[1]<1){
      item.Z = ("");
      item.A = (""); 
      item.mass = ("");
      // try if its mocadi matter
      res[1] = MOCADI_Matter.GetZ(val);
    }
    
    if(res[0]>0){
      item.mass = (NucData.GetNucData(res[0],res[1])["M"].toPrecision(7));
      item.A = res[0].toString();
      }
      
    if(res[0]===0 && res[1]>0){
      item.A = "";
      item.mass = ElementMass.GetMass(res[1]).toPrecision(7);
      }
    
    if(res[1]>0){
        var density;
        var gas;
        item.Z = res[1].toString();
        density = GetDensity(res[1]);
        gas = (density<0.01 && density >0)?1:0;
        item.rho = density;
        item.gas = gas;
      } 
      
    item.symbol = (val);
    this.changel(index,item.l);
  };

  MatterEditor.ui.changeZ = function(index, val){
    var item = MatterEditor.vm.layers[index];
    var num = parseInt(val);
    item.Z = val;
    item.I = "";
    if(isNaN(num) || num < 1){
      item.A =  ""; 
      item.mass = "";
      item.symbol = "";
      return;
      }
    
    var numa = parseInt(item.A);
    if(isNaN(numa) || numa<1)numa = 0;
    
    var symbol;
    var density;
    var gas;
    
    if(num<120){
        symbol = NucID.GetSymbol(numa,num);
        if(numa>0){
            MatterEditor.vm.layers[index].mass = (NucData.GetNucData(numa,num)["M"].toPrecision(7));
          }
        else{
            MatterEditor.vm.layers[index].mass = (ElementMass.GetMass(num).toPrecision(7));
            }
        }
    if(num>200){
        var r = MOCADI_Matter.Get(num);
        symbol = (r.key)?r.key:"";
        MatterEditor.vm.layers[index].mass = "";
        MatterEditor.vm.layers[index].A = "";
        }
        
    density = GetDensity(num);
    gas = (density<0.01 && density >0)?1:0;
    
    item.symbol = (symbol);
    item.rho = (density);
    item.gas = (gas);
    
    this.changel(index,item.l);  
  };
 
  MatterEditor.ui.changeA = function(index, val){
    var item = MatterEditor.vm.layers[index];
    var num = parseInt(val);
    var numz = parseInt(item.Z);
    if(isNaN(num) || num < 0){
        var symbol = NucID.GetSymbol(0,numz);
        item.symbol = symbol;
        item.A = "";
        item.mass = (ElementMass.GetMass(numz).toPrecision(7));
        m.redraw();
        return;
        }
        
    item.A = (num);
    var res = NucID.GetSymbol(num,numz);
    item.symbol = (res);
    var ndata = NucData.GetNucData(num,numz);
    if(ndata["M"]){
        item.mass = (ndata["M"].toPrecision(7));
    }
    else{
        item.mass = "";
    }
    
    this.changel(index,item.l);
    m.redraw();
  };


  MatterEditor.ui.changerho = function(index, val){
        var item = MatterEditor.vm.layers[index];
        
        var rho = parseFloat(val);
        var l = parseFloat(item.l);
        item.rho = val;
        
        if(isNaN(rho) || rho < 0){
            return;
            }
        
        var th = l*rho*1000;
        item.th = (th.toPrecision(3));
        m.redraw();
    };

MatterEditor.ui.changel = function(index, val){
        var item = MatterEditor.vm.layers[index];
        
        var l = parseFloat(val);
        var rho = parseFloat(item.rho);
        var th = parseFloat(item.th);
        item.l = val;
        if(isNaN(l) || l < 0)return;
        if(isNaN(rho) || rho < 0)return;
        
        th = l*rho*1000;
        item.th = (th.toPrecision(4));
        m.redraw();
    };

MatterEditor.ui.changeth = function(index, val){
        var item = MatterEditor.vm.layers[index];
        var th = parseFloat(val);
        var rho = parseFloat(item.rho);
        var l = parseFloat(item.l);
        item.th = val;
        if(isNaN(th) || th < 0)return;
        if(isNaN(rho) || rho < 0)return;
          
        l = th/rho/1000;
        item.l = (l.toPrecision(4));
        m.redraw();
    };

MatterEditor.ui.Remove = function(index){
    if(MatterEditor.vm.editindex==-1){
        MatterEditor.vm.layers.splice(index,1);
        App.clear_cache();
        App.Save();
        MatterEditor.vm.calculate();
        m.redraw();
    }
  };

  MatterEditor.ui.Add = function(){
    MatterEditor.vm.editindex = MatterEditor.vm.layers.length;
    MatterEditor.vm.layers.push( new MatterLayer({"symbol":"","name":"","A":"","Z":"","mass":"","rho":0,"th":0,"gas":false}));
  };

  MatterEditor.ui.Down = function(index){
    if(index>= (MatterEditor.vm.layers.length - 1))return;
    if(MatterEditor.vm.editindex==-1){
        var cur = MatterEditor.vm.layers[index];
        MatterEditor.vm.layers[index] = MatterEditor.vm.layers[index+1];
        MatterEditor.vm.layers[index+1] = cur;
        App.clear_cache();
        App.Save();
        m.redraw();
    }
  };

    MatterEditor.ui.Up = function(index){
    if(index<1)return;
    if(MatterEditor.vm.editindex==-1){
        var cur = MatterEditor.vm.layers[index];
        MatterEditor.vm.layers[index] = MatterEditor.vm.layers[index-1];
        MatterEditor.vm.layers[index-1] = cur;
        App.clear_cache();
        App.Save();
        m.redraw();
    }
  };

  MatterEditor.ui.EditDone = function(){
      
      var item = MatterEditor.vm.layers[MatterEditor.vm.editindex];
      
      if(item.Z===""){
          alert("Wrong matter defined, matter removed");
          MatterEditor.vm.editindex = -1;
          MatterEditor.ui.Remove(MatterEditor.vm.editindex);
      }
      
      if(item.name===""){
        item.name = ("Material_"+MatterEditor.vm.editindex);
      }
      
      if( isNaN(parseFloat(item.l)) ){
          item.l = 0;
          item.th = 0;
          }
          
      if( isNaN(parseFloat(item.th)) ){
          item.l = 0;
          item.th = 0;
          }
        
      if( isNaN(parseFloat(item.rho)) ){
          item.rho = GetDensity(item.Z);
          }
          
      item.l = parseFloat(item.l);
      item.th = parseFloat(item.th);
      item.rho = parseFloat(item.rho);
      
      MatterEditor.vm.editindex = -1;
      App.clear_cache();
      App.Save();
      MatterEditor.vm.calculate();
  };

  MatterEditor.ui.Edit = function(index){
      if(MatterEditor.vm.editindex==-1){
        MatterEditor.vm.editindex = index;
        m.redraw();
        }
  };


