              m("label",{class:styles.label,for:"input_material_th"},m.trust("Thickness<br>(mg/cm<sup>2</sub>)")),
              m("input",{type:"text",style:input_style,id:"input_material_th", oninput:withAttr_nr("value",ctrl.changeth.bind(ctrl,index)),value:item.th})
