var thickness_calculator2 = {};
thickness_calculator2.th = 0.0;
thickness_calculator2.l = 0.0;
thickness_calculator2.calculate_th = function(index,type, val){
    //var item = MatterEditor.vm.layers[index];
    var rho = MatterEditor.vm.layers[index].rho;
    rho = parseFloat(rho);
    var num= parseFloat(val);
    if(isNaN(num) || num<0){
        console.log("could not parse number");
        return;
        }
    Catima.get_energy_out().then(
        function(eouts){
            if(index>=eouts.length){
                return -1;
                }
            var Ein = (index>0)?eouts[index-1]:App.projectile.state.T;
            return Ein;
            }).then(function(T){
                thickness_calculator2.th = -1;
                thickness_calculator2.l = -1;
                if(T<0)return;
                if(type == 0 && num>T)return;
                if(type == 1 && num>1.0)return;
                
                Catima.get_data(Catima.prepare_one(index)).then(function(res){
                    var srange = new spline(res[0].energy, res[0].range);
                    if(type==0){                 
                        thickness_calculator2.th = 1000*(srange.at(T)-srange.at(num));
                    }
                    else{
                        thickness_calculator2.th = 1000*(srange.at(T)*num);
                        }
                    thickness_calculator2.l = thickness_calculator2.th/rho/1000;
                    m.redraw();
                });            
                
                });
  };


var ThicknessCalculator = {};

ThicknessCalculator.submit = function(index,th, callback){
    if(th>0.0){
      MatterEditor.ui.changeth(index,th.toFixed(2));
      if(callback)callback.apply(this);
      }
  };

ThicknessCalculator.oninit = function(){
  thickness_calculator2.th = 0.0;
  thickness_calculator2.l = 0.0;
};

ThicknessCalculator.view = function(vnode){
  var index = vnode.attrs.index;
  var vm = thickness_calculator2;
  var input_style = "width:7em;";
  
  return  m(ToggleComponent,{id:"_thcal", title:"Calculate", 
      content: m("div",{class:styles.div_column},[
                m("div","Adjust thickness to: "),
                m("label",{class:styles.label,for:"input_Eout", title:"Enter Energy after material"},"Eout (MeV/u)"),
                m("input",{type:"text",style:input_style,
                            id:"input_Eout", 
                            oninput:withAttr_nr("value",debounce(vm.calculate_th.bind(vnode.state,index,0),500))
                            }),
                m("label",{class:styles.label,for:"input_RangeFraction", title:"Enter Fraction of Range"},"th/range"),
                m("input",{type:"text",style:input_style,
                            id:"input_RangeFraction", 
                            oninput:withAttr_nr("value",debounce(vm.calculate_th.bind(vnode.state,index,1),500))
                            }),
                (vm.th>=0.0)?m("div",{class:styles.div_cell},[
                        "Th. = "+vm.th.toFixed(2),m.trust(" mg/cm<sup>2</sup><br>"),
                        "Length    = "+vm.l.toFixed(5)+" cm"
                        ]):m("div",text_error("Wrong value!")),
                (vm.th>0.0)?button("Submit Thickness",vnode.state.submit.bind(vnode.state,index,vm.th,ToggleComponent.off.bind(this,"_thcal"))):null
              ])
      });
              
  /*
  return m("div",{class:styles.form_element},[
                m("div","Adjust thickness to: "),
                m("label",{class:styles.label,for:"input_Eout", title:"Enter Energy after material"},"Eout (MeV/u)"),
                m("input",{type:"text",style:input_style,
                            id:"input_Eout", 
                            oninput:withAttr_nr("value",debounce(vm.calculate_th.bind(vnode.state,index),500))
                            }),
                (vm.th>=0.0)?m("div",{class:styles.div_cell},[
                        "Th. = "+vm.th.toFixed(2),m.trust(" mg/cm<sup>2</sup><br>"),
                        "Length    = "+vm.l.toFixed(5)+" cm"
                        ]):m("div",text_error("Wrong value!")),
                (vm.th>0.0)?button("Submit Thickness",vnode.state.submit.bind(vnode.state,index,vm.th)):null
              ]);
*/
};

export {MatterEditor};
