import {append_script} from "./utils.js";

var vega_loaded = false;
function load_vega(){
    //if(typeof vega === "undefined"){
        return append_script([
            "https://cdn.jsdelivr.net/npm/vega@4.2.0",
            "https://cdn.jsdelivr.net/npm/vega-lite@3.0.0-rc0",
            "https://cdn.jsdelivr.net/npm/vega-embed@3.18.1"
            ]).then(function(){
                vega_loaded = true;
                return vega_loaded;
                });
      //  }
}

export function vegaplot(d){
    this.id = d.id || "catimaplot";
    this.values = d.values||[];
    this.specs = {
        "$schema": "https://vega.github.io/schema/vega-lite/v2.json",
        "title" : "",
        "autosize":{"type":"fit"},
        "width":d.width||800,
        "height":d.height||600,
        "data": {
          "values": this.values
            },
        "selection":{
                "grid":{"type":"interval","bind":"scales"}
            },
        
            "mark": d.mark||"circle",
            "encoding": {
                "x": {"field": d.x||"", "type": "quantitative","scale":{"type":d.x_axis_type||"linear","zero":false},"axis":{"title":d.x_label||""}},
                "y": {"field": d.y||"", "type": "quantitative","scale":{"type":d.y_axis_type||"linear","zero":false},"axis":{"title":d.y_label||""}},
                "color" : {"field" : d.color||"", "type":"nominal", "legend":d.legend||null} 
                }
    };
}
vegaplot.prototype.set_values = function(v){
    this.specs.data.values = v;
};

vegaplot.prototype.set_width = function(w){
    this.specs.width = w;
};

vegaplot.prototype.set_height = function(h){
    this.specs.height = h;
};

vegaplot.prototype.set_title = function(title){
    this.specs.title = title;
};

vegaplot.prototype.set_x = function(v, title){
  this.specs.encoding.x.field = v;
  if(title)
    this.set_x_label(title);
};

vegaplot.prototype.set_x_label = function(title){
  this.specs.encoding.x.axis.title = title;
};

vegaplot.prototype.set_x_axis_type = function(type){
  this.specs.encoding.x.scale.type = type;
};

vegaplot.prototype.set_y_axis_type = function(type){
  this.specs.encoding.y.scale.type = type;
};


vegaplot.prototype.set_y = function(v, title){
  this.specs.encoding.y.field = v;
  if(title)
    this.set_y_label(title);
};

vegaplot.prototype.set_y_label = function(title){
  this.specs.encoding.y.axis.title = title;
};

vegaplot.prototype.set_color_field = function(val){
    this.specs.encoding.color.field = val;
    };

vegaplot.prototype.set_legend_title = function(val){
    this.specs.encoding.color.legend.title = val;
    };

vegaplot.prototype.show = function(){
    if(vega_loaded){
        vegaEmbed("#"+this.id,this.specs,{"mode":"vega-lite","actions":false});
        }
    else{
        var plot = this;
        load_vega().then(function(){
            vegaEmbed("#"+plot.id,plot.specs,{"mode":"vega-lite","actions":false});
            });
        }
    };
