import Tingle from "./tingle.js";

export var Modal = {};
Modal.modal = new Tingle({footer: true,closeLabel: "Close"});

Modal.modal.addFooterBtn('Close', 'tingle-btn', function() {
    Modal.modal.close();
});

Modal.set_content = function(content){
    Modal.modal.setContent(content);
};

Modal.open = function(){
    Modal.modal.open();
};

Modal.close = function(){
    Modal.modal.close();
};
