import {MOCADI_Matter} from "../vendor/mocadimatter.js";
import {vegaplot} from "./VegaPlotComponent.js";
import {Catima} from "./catima.js";
import {App} from "./app.js";
import {NucID} from "../vendor/elements";

var vplot_defaults = {
    "x":"th",
    "y":"Eout",
    "x_label":"thickness (g/cm2)",
    "y_label":"Energy out (MeV/u)",
    "color":"material"
};

var MaterialPlotData = {};
MaterialPlotData.values = [];
MaterialPlotData.status = "";

MaterialPlotData.reset = function(){
    MaterialPlotData.fig = new vegaplot(vplot_defaults);
    MaterialPlotData.values = [];
    MaterialPlotData.status = "";
};

MaterialPlotData.update = function(){
    MaterialPlotData.fig.show();
};

MaterialPlotData.matname = "";
// sort raw data to data format for plotting
MaterialPlotData.sort = function(data){
    MaterialPlotData.values = [];
    if(data.length != 1)return;
    var entry = data[0];
    var res = Catima.Eout_vs_thickness(entry,App.projectile.state.T);
    for(var i=0;i<res[0].length;i++){                              
        var name = (entry.Zm<201)?NucID.GetSymbol(entry.Am,entry.Zm):MOCADI_Matter.Get(entry.Zm).name;
        MaterialPlotData.matname = name;
        MaterialPlotData.values.push({"th":res[0][i],"Eout":res[1][i],"Eloss":res[2][i],"dEdx":res[3][i],"material":name});
        }
    MaterialPlotData.fig.set_values(MaterialPlotData.values);
};

MaterialPlotData.plot_options = [
    {value:"Eout", name:"Energy after"},
    {value:"Eloss", name:"Energy loss"},
    {value:"dEdx", name:"dEdx"}
];

MaterialPlotData.change_y_axis = function(col){
    if(col=="Eout"){
        MaterialPlotData.fig.specs.title = "material: "+MaterialPlotData.matname+", projectile: "+App.projectile.GetNucleusSymbol()+", T = "+App.projectile.state.T.toPrecision(3) + "MeV/u";
        /*
        MaterialPlotData.specs.fig.layer[0].encoding.y.axis.title = "Eout [MeV/u]";
        MaterialPlotData.specs.fig.layer[0].encoding.y.scale.type = "linear";
        MaterialPlotData.specs.fig.layer[0].encoding.y.field = col;
        MaterialPlotData.specs.fig.layer[1].encoding.y.field = col;
        */ 
        MaterialPlotData.fig.specs.encoding.y.axis.title = "Eout [MeV/u]";
        MaterialPlotData.fig.specs.encoding.y.scale.type = "linear";
        MaterialPlotData.fig.specs.encoding.y.field = col;
    }
    if(col=="Eloss"){
        /*
        MaterialPlotData.specs.fig.title = "Energy loss in material, projectile: "+App.projectile.GetNucleusSymbol()+", T = "+App.projectile.state.T + "MeV/u";
        MaterialPlotData.specs.fig.layer[0].encoding.y.axis.title = "Energy loss [MeV]";
        MaterialPlotData.specs.fig.layer[0].encoding.y.scale.type = "linear";
        MaterialPlotData.specs.fig.layer[0].encoding.y.field = col;
        MaterialPlotData.specs.fig.layer[1].encoding.y.field = col;
        */ 

        MaterialPlotData.fig.specs.title = "material: "+MaterialPlotData.matname+", projectile: "+App.projectile.GetNucleusSymbol()+", T = "+App.projectile.state.T.toPrecision(3) + "MeV/u";
        MaterialPlotData.fig.specs.encoding.y.axis.title = "Energy loss [MeV]";
        MaterialPlotData.fig.specs.encoding.y.scale.type = "linear";
        MaterialPlotData.fig.specs.encoding.y.field = col;
    }
    if(col=="dEdx"){
        /*
        MaterialPlotData.specs.fig.title = "Energy loss in material, projectile: "+App.projectile.GetNucleusSymbol()+", T = "+App.projectile.state.T + "MeV/u";
        MaterialPlotData.specs.fig.layer[0].encoding.y.axis.title = "dEdx [MeV/g/cm2]";
        MaterialPlotData.specs.fig.layer[0].encoding.y.scale.type = "log";
        MaterialPlotData.specs.fig.layer[0].encoding.y.field = col;
        MaterialPlotData.specs.fig.layer[1].encoding.y.field = col;
        */ 

        MaterialPlotData.fig.specs.title = "material: "+MaterialPlotData.matname+", projectile: "+App.projectile.GetNucleusSymbol()+", T = "+App.projectile.state.T.toPrecision(3) + "MeV/u";
        MaterialPlotData.fig.specs.encoding.y.axis.title = "dEdx [MeV/g/cm2]";
        MaterialPlotData.fig.specs.encoding.y.scale.type = "log";
        MaterialPlotData.fig.specs.encoding.y.field = col;
    }
   
    
    MaterialPlotData.update();
};

export {MaterialPlotData};
