export function debounce(func, wait, context) {
  var timeout;

  return function executedFunction() {
    var args = arguments;
    var later = function() {
      timeout = null;
      func.apply(context||this, args);
    };

    clearTimeout(timeout);
    timeout = setTimeout(later, wait||250);
  };
}

export function enterescape(onenter, onescape) {
	return function (e) {
    e.redraw = false;
		if (e.keyCode === 13) {
			onenter();
            m.redraw();
		} else if (e.keyCode === 27) {
			onescape();
            m.redraw();
		}
        
	};
}

export function escape(onescape) {
	return function (e) {
    e.redraw = false;
		if (e.keyCode === 27) {
			onescape();
            m.redraw();
		}
	};
}

export function enter(onescape) {
	return function (e) {
    e.redraw = false;
		if (e.keyCode === 13) {
			onescape();
            m.redraw();
		}
	};
}

