export function button_route(name, route, bstyle){
  return m("a", {class:(bstyle)?bstyle:styles.button, onclick: function(){ m.route.set(route); }}, name );
}

export function button(name, fun, bstyle){
  return m("a",{class:(bstyle)?bstyle:styles.button,onclick:fun},name);
}

export function text_error(txt){
    return m("span",{class:styles.color_error},txt);
}

export function badge_error(txt){
    return m("div",{class:styles.error_badge},txt); 
}

export function SaveFile(v,name){
  var blob = new Blob([(typeof v ==="string")?v:JSON.stringify(v,null,'\t')], {type: "text/plain;charset=utf-8"});
  saveAs(blob, name);
}


export function FileLoader(args,f){
    return [m("input",{class:"my-remove",type:"file",id:args.id,onchange:function(e){f(e.target.files[0]);e.target.value=null;}}),    
    m("a",{class:args.class,onclick:function(){document.getElementById(args.id).click();}},args.title)];
}

export function withAttr_nr(attrName, callback1, context) {
	return function(e) {
    e.redraw = false;
    callback1.call(context || this, attrName in e.currentTarget ? e.currentTarget[attrName] : e.currentTarget.getAttribute(attrName));
	};
}

export function check_set(attrName, newattr, callback1, context) {
    return function(e) {
	e.redraw = false;
	var r = callback1.call(context, attrName in e.currentTarget ? e.currentTarget[attrName] : e.currentTarget.getAttribute(attrName));
	if(r){
	    if(newattr in context){
		e.currentTarget.attrName = context.newattr;
		e.redraw = true;
		}
	}
	else {
	    e.currentTarget.attrName = "";
	    }
	};
}

export function noredraw(callback, context){
  return function(e){
    e.redraw = false;
    callback.call(context||this);
  };
}

export function append_script(list){
    var promises = [];
    list.forEach(function(src) {
        var p = new Promise(function(resolve,reject){
            var script = document.createElement('script');
            script.src = src;
            script.async = false;
            script.onload = function(){
                    resolve();
                };
            script.onerror = function(){
                    reject();
                };
            document.head.appendChild(script);
            });
        promises.push(p);
        });
    return Promise.all(promises);
    }


export function get_window_size(){
  var w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
  var h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
  return [w,h];
}

export function get_div_size(id){
  var w = document.getElementById(id).clientWidth;
  var h = document.getElementById(id).clientHeight;
  return [w,h];
}

export function to_string(val, num)
{
        var n = num || 4;
        if(val<Math.pow(10,-n+1)){
            return val.toExponential(n);
            }
        else {
            return val.toFixed(n);
            }
}


export function linspace_array(a,b,num){
    if(a>=b || num<2)return;
    var res = new Float64Array(num);
    var step = (b-a)/(num-1);
    for(var i=0;i<num-1;i++){
        res[i] = a + (step*i);
        }
    res[num-1] = b;
    return res;
}
