import {catima_config} from "./catima_config.js";
import {MatterEditor, MatterLayer} from "./material_editor_component.js";
import {StorageAPI} from "../vendor/storage";
import {Catima} from "./catima.js";
import Raven from 'raven-js';
import { append_script } from "./utils.js";
import {SharpCalculator} from "../vendor/calculator";

export var App = App||{};
App.state = {};
App.state.nuc = "";
App.state.T = 0;
App.state.matter = [];
App.state.config = new catima_config();
App.info = "";
App.projectile = new SharpCalculator();
App.projectile.Set.T(100.0);
App.MatterEditor = MatterEditor;

window.App = App;

App.LoadJSONFile = function(file){
    var reader = new FileReader();
    reader.onload = (function(){
                        return function(_e){
                            try{
                                App.Load(JSON.parse(_e.target.result));
                                App.Save();
                            }
                            catch(err){
                                alert("Failed to load file");
                            }
                            finally{
                                m.redraw();
                            }
                            };
                    })(file);
    reader.readAsText(file);
};

App.AddLayersFromFile = function(file){
    var reader = new FileReader();
    reader.onload = (function(){
                        return function(_e){
                            try{
                                App.AddLayers(JSON.parse(_e.target.result));
                                App.Save();
                            }
                            catch(err){
                                alert("Failed to add material layers");
                                App.report(err);
                            }
                            finally{
                                m.redraw();
                            }
                            };
                    })(file);
    reader.readAsText(file);
};

App.get_editindex = function(){
    return MatterEditor.vm.editindex;
};

App.get_edit_layer = function(){
    if(MatterEditor.vm.editindex>-1)
        return MatterEditor.vm.layers[MatterEditor.vm.editindex];
    else return null;
};

App.Load = function(data){
    if(!data){
        return;
    }

    // old style
    if(data.hasOwnProperty("nuc") && Array.isArray(data.nuc)){
        App.projectile.SetNucleus(data.nuc[0],data.nuc[1],data.nuc[2]);
    }

    if(data.hasOwnProperty("T")){
        App.projectile.Set.T(data.T);
    }
    
    // new style
    if(data.hasOwnProperty("projectile") && data.projectile.hasOwnProperty("A") && data.projectile.hasOwnProperty("Z")){
        App.projectile.SetNucleus(data.projectile.A,data.projectile.Z,data.projectile.Q);
        if(data.projectile.hasOwnProperty("T")){
            App.projectile.Set.T(data.projectile.T);
        }
    }
    
    MatterEditor.vm.layers.length = 0; // delete current data
    App.AddLayers(data);
    MatterEditor.vm.calculate();

    if(data.config){
        App.state.config.set(data.config);
    }
    App.loaded  = true;
    App.Save();
};

App.AddLayers = function(data){
    if(!data.matter)return;
    for(var i=0;i<data.matter.length;i++){
            var layer = data.matter[i];
            if(Array.isArray(layer)){ // this comes from server where is it saved as array
                var ll = {};
                ll.name = "matter"+i;
                ll.A = layer[0];
                ll.Z = layer[1];
                ll.rho = layer[2];
                ll.th = layer[3];
                ll.gas = layer[4];
                ll.mass = layer[5];
                ll.I = layer[6];
                MatterEditor.vm.layers.push( new MatterLayer(ll));
            }
            else{ // comes from file as dictionary
                MatterEditor.vm.layers.push( new MatterLayer(layer));
                }        
        }
};

App.Save = function(){
    App.state.nuc = App.projectile.GetNucleus();
    App.state.T = App.projectile.state.T;
    App.state.matter = MatterEditor.vm.layers;
    App.state.units = [1,1000,1];
    StorageAPI.save("state",App.state);
};

App.DeleteStorage = function(){
    localStorage.clear();
};

App.MOCADIstr = function(){
    var nuc = App.projectile.GetNucleus();
    var T = App.projectile.state.T;
    var matter = MatterEditor.vm.layers;
    
    var mocadistr = "epax 2\natima-1.0\noption listmode tree\n";
    // beam keyword
    mocadistr = mocadistr + "BEAM\n20000\n" + T +", 0, "+nuc[0]+", "+nuc[1]+", "+(nuc[1]-nuc[2])+"\n";
    mocadistr = mocadistr + "2\n0, 0, 0, 0, 0\n";
    mocadistr = mocadistr + "2\n0, 0, 0, 0, 0\n";
    mocadistr = mocadistr + "2\n0, 0, 0, 0, 0\n";
    
    //save start
    mocadistr = mocadistr + "*\nERWARTUNGSWERTE START\nSAVE START";
    
    //matters
    for(var i=0;i<matter.length;i++){
        var mstr = "\n*** "+matter[i].name+" ***\nMATTER\n"+(matter[i].A||0)+", "+matter[i].Z+", "+(1000.0*matter[i].rho);
        mstr = mstr+"\n3, "+matter[i].th; //converto to mg/cm2 as in mocadi
        mstr = mstr+"\n0\n0, 0, 0";
        mstr = mstr+"\n1, 1, 0\n0";
        mocadistr = mocadistr+mstr;
    }
    //save end
    mocadistr = mocadistr + "\n*\nERWARTUNGSWERTE END\nSAVE END";
    mocadistr = mocadistr + "\nEND";
    return mocadistr; 
};

App.fileapi = false;
App.datavalid = false;
App.loaded = false;
App.clear_cache = function(){
    App.datavalid = false;
    if(Catima){
		Catima.clear();
		}
};

/////////////// Browser Check ///////////////
App.CheckBrowser = function(){
    var r = true;
    if(!window.FileReader)r=false;
    //if(!window.Worker)r=false;
    if(!window.localStorage)r=false;
    if(!('startsWith' in String.prototype)) {
        var features = ['String.prototype.startsWith'];
        var src = 'https://cdn.polyfill.io/v2/polyfill.min.js?features='+features.join(',')+'&flags=gated,always';
        append_script([src]);
    }

    return r;
    };

App.load_config = function(uid){
    return Catima.load_config(uid).then(function(res){
        if(res.error || !res.input){
            alert("Failed to load remote configuration:"+res.error);
            App.report("failed to load remote config, uid="+uid);
        }
        App.Load(res.input);

    }).catch(function(){
        alert("Failed to load remote configuration");
        App.report("failed to load remote config, uid="+uid);
    });
};

App.report = function(e, info){
    console.log(e);
    var extra = null;
    if(info){
        extra = {extra:info};
    }
    if(typeof e === "string")
        Raven.captureMessage(e,extra);  
    else 
        Raven.captureException(e,extra);  
};
