/// CardComponent ///
import {styles} from "./styles.js";
import {button, get_div_size, SaveFile} from "./utils.js";

var NavigationComponent = {};

NavigationComponent.view = function(vnode){
       var links = vnode.attrs.links;
       return m("nav",{class:styles.nav},links);
};

var CardComponent = {};
CardComponent.view = function(vnode){
  return m("article",{class:styles.card,style:"overflow:hidden;"},[
              m("div",{class:styles.card_header},vnode.attrs.title),
              m("div",{class:styles.card_main},vnode.attrs.content)
              ]);
};

var ToggleComponent = {};
ToggleComponent.visible = false;
ToggleComponent.off = function(id){
    var el = document.getElementById(id);
    el.style.display = "none";
    ToggleComponent.visible = false;
};
ToggleComponent.toggle = function(id){
	var el = document.getElementById(id);
        if(ToggleComponent.visible)el.style.display = "none";
        else el.style.display = "block";
    ToggleComponent.visible = !ToggleComponent.visible;
};
ToggleComponent.oninit = function(){
    ToggleComponent.visible = false;
};

ToggleComponent.view = function(vnode){
    var args = vnode.attrs;
    var name = (ToggleComponent.visible)?args.title+" ^":args.title+" +";
            
            return m("div",{class:(ToggleComponent.visible)?styles.box:""},[
                button(name,ToggleComponent.toggle.bind(vnode.state,args.id),styles.button_full),
                m("div",{class:styles.div_cell,id:args.id,
			 style:"display:none;"
			},args.content)
            ]);
};


var PlotComponent = {};
PlotComponent.oncreate = function(vnode){
    var PlotObject = vnode.attrs;
    if(PlotObject.values.length<1)return null;
    //if(PlotDensity.values.length<1)return;
    var dsize = get_div_size(PlotObject.fig.id);
    var width  = dsize[0]-90;
    var height = (dsize[0]*2.0/3.0)|0;
    if(width<400)width = 400;
    if(height<300)height = 300;
    PlotObject.fig.set_width(width);
    PlotObject.fig.set_height(height);
    PlotObject.fig.show();
};

PlotComponent.view = function(vnode){
    var PlotObject = vnode.attrs;
    if(PlotObject.values.length<1){
        return null;
        }
    return [m("div",{class:styles.div_row},
            [
             (PlotObject.plot_options)?m("div",{class:styles.div_cell_20},[
                m("label",{class:styles.label},"Plot"),
                m("select",{onchange:m.withAttr("value",PlotObject.change_y_axis)},
                        PlotObject.plot_options.map(function(e){
                            return m("option",{value:e.value},e.name);
                        })
                        )
                ]
             ):null,
             m("div",{class:styles.div_cell_80, id:PlotObject.fig.id}
                //(PlotObject.ready())?
                //m(VegaPlot,{status:"loading",data:PlotObject.fig})
              )
            ]),
            m("div",{class:styles.div_row},button("Save Data",function(){
                var keys = Object.keys(PlotObject.values[0]);
                var data = PlotObject.values.map(function(item){
                    return keys.map(function(k){return (typeof item[k] === "string")?item[k]:item[k].toPrecision(5);}).join(" ");
                });
                SaveFile(keys.join(" ")+"\n"+data.join("\n"),"data.txt");
            }))
        ];
};

export {PlotComponent, CardComponent, NavigationComponent, ToggleComponent};
