import {epax, goldhaber} from "../vendor/epax";
import {styles} from "./styles";
import {debounce} from "./utils";
import {NucID} from "../vendor/elements";
import {NucKinematics} from "../vendor/kinematics";
import {App} from "./app";

var EpaxModel = {
    projectile: "",
    energy:"",
    target: "",
    fragment: "",
    cs : -1.0,
    sigma_p : -1.0,
    p:-1.0,
    momentum_deviation : -1.0
};

EpaxModel.clear = function(){
    EpaxModel.cs = -1.0;
    EpaxModel.sigma_p = -1.0;
    EpaxModel.p = -1.0;
    EpaxModel.momentum_deviation = -1.0;
};

EpaxModel.calculate = debounce(function(){
    if(EpaxModel.projectile.length<2 ||EpaxModel.target.length<2 || EpaxModel.fragment.length<2)return;
    var nuc;
    nuc = NucID.GetNuc(EpaxModel.projectile);
    var ap = nuc[0];
    var zp = nuc[1];    
    nuc = NucID.GetNuc(EpaxModel.target);
    var at = nuc[0];
    var zt = nuc[1];
    nuc = NucID.GetNuc(EpaxModel.fragment);
    var af = nuc[0];
    var zf = nuc[1];
    EpaxModel.clear();
    if(ap<=0 || af <= 0 || at <=0)return;
    if(isNaN(ap) || isNaN(zp) || isNaN(at) || isNaN(zt) || isNaN(af) || isNaN(zf)) return;
    if(ap<zp || af<zf || at<zt || ap<=af)return;
    EpaxModel.projectile = NucID.GetSymbol(ap, zp);
    EpaxModel.target = NucID.GetSymbol(at, zt);
    EpaxModel.fragment = NucID.GetSymbol(af, zf);
    EpaxModel.cs = epax(ap, zp, at, zt, af, zf).toFixed(3);

    var e = parseFloat(EpaxModel.energy);
    if(isNaN(e))return;
    EpaxModel.sigma_p = goldhaber(ap, af);
    EpaxModel.p = NucKinematics.p_from_T(e, af);
    EpaxModel.momentum_deviation = EpaxModel.sigma_p/EpaxModel.p;

    m.redraw();
},250);

export var EpaxComponent = {};

EpaxComponent.input_function = function(attr, model, placeholder){   
    return m("label",{class:styles.label},[
        attr+":",
        m("input",{type:"text", style:"width:7em;", cplaceholder:placeholder, 
            value:model[attr],
            oninput:function(e){
                e.redraw = false;
                model[attr] = e.currentTarget["value"];
                model.calculate();
            }})
    ]);
};

EpaxComponent.oninit = function(){
    EpaxModel.clear();
    EpaxModel.projectile = App.projectile.state.Symbol;
    EpaxModel.energy = App.projectile.state.T;
    if(EpaxModel.target.length>2 && EpaxModel.fragment.length>2){
        EpaxModel.calculate();
    }
};

EpaxComponent.view = function(){
    var model = EpaxModel;
    var cs = parseFloat(model.cs);
    var e = parseFloat(model.energy);
    var momentum_deviation = parseFloat(model.momentum_deviation);
    var p = parseFloat(model.p);
    console.log(EpaxModel);
    return [
        m("h3",{class:styles.h3},"EPAX v3 + Goldhaber"),
        m("div",{class:styles.div_row_centered},[
            EpaxComponent.input_function("projectile",model, "e.g. 12C"),
            EpaxComponent.input_function("target",model, "e.g 9Be"),
            EpaxComponent.input_function("fragment",model, "e.g. 11C"),
        ]),
        m("div",{class:styles.div_row_centered},[
            EpaxComponent.input_function("energy",model, "in MeV/u"),
            "MeV/u",
            (!isNaN(p) && p>0)?" = "+model.p.toFixed(1) + " MeV/c":null

        ]),
        m("div",{class:styles.div_row_centered},[
            (isNaN(cs) || cs<=0)?null:
            m("div",{class:styles.div_cell_100}, m.trust("&sigma; = "+ model.cs+" mb")),
        (isNaN(e) || momentum_deviation<0.0|| isNaN(momentum_deviation) )?null:
            m("div",{class:styles.div_cell_100},[
                 m.trust("&#963<sub>p</sub> / p = "), 
                 model.momentum_deviation.toPrecision(4),
                ])
        ])
    ];
};
