import {MOCADI_Matter} from "../vendor/mocadimatter.js";
import {vegaplot} from "./VegaPlotComponent.js";
import {App} from "./app.js";
import {NucID} from "../vendor/elements";

var vplot_defaults = {
    "x":"energy",
    "y":"dEdx",
    "x_axis_type":"log",
    "y_axis_type":"log",
    "x_label":"Energy (MeV/u)",
    "y_label":"dE/dx [MeV/g/cm2]",
    "color":"material",
    "legend":{}
};

var PlotData = {};
PlotData.values = [];
PlotData.status = "";

PlotData.reset = function(){
    PlotData.fig = new vegaplot(vplot_defaults);
    PlotData.values = [];
    PlotData.status = "";
};

PlotData.update = function(){
    PlotData.fig.show();
};

// sort raw data to data format for plotting
PlotData.sort = function(data){
    PlotData.values = [];
    if(data.length < 1)return;
    data.forEach(function(entry){
        for(var i=1;i<entry.energy.length;i++){                              
            var name = (entry.Zm<201)?NucID.GetSymbol(entry.Am,entry.Zm):MOCADI_Matter.Get(entry.Zm).name;
            PlotData.values.push({"energy":entry.energy[i],"dEdx":entry.dEdx[i],"range":entry.range[i],"material":name});
        }
    });
    PlotData.fig.set_title("projectile: "+App.projectile.GetNucleusSymbol());
    PlotData.fig.set_values(PlotData.values);
};


PlotData.plot_options = [
    {value:"dEdx", name:"dEdx"},
    {value:"range", name:"Range"}
];

PlotData.change_y_axis = function(col){
    if(col=="dEdx"){
        PlotData.fig.specs.encoding.y.axis.title = "dE/dx [MeV/g/cm2]";
        PlotData.fig.specs.encoding.y.field = col;
        PlotData.fig.specs.encoding.y.scale.type = "log";
    }
    if(col=="range"){
        PlotData.fig.specs.encoding.y.axis.title = "range [g/cm2]";
        PlotData.fig.specs.encoding.y.field = col;
        PlotData.fig.specs.encoding.y.scale.type = "log";
    }
    PlotData.update();
};

export {PlotData};
