import {styles} from "./styles.js";
import {button, button_route, text_error} from "./utils.js";
import {Catima} from "./catima.js";
import {App} from "./app.js";
import {NucData} from "../vendor/nucdata";

var NucleusSelector = {};

NucleusSelector.ui = {};
NucleusSelector.nuc = {};
NucleusSelector.oninit = function(){   
    NucleusSelector.nuc = App.projectile;
    NucleusSelector.ui.uiupdate();
    App.clear_cache();
};

NucleusSelector.ui.uiupdate = function(){
     
      NucleusSelector.ui.Symbol = NucleusSelector.nuc.state.Symbol;
      NucleusSelector.ui.A = NucleusSelector.nuc.state.A;
      NucleusSelector.ui.Z = NucleusSelector.nuc.state.Z;
      NucleusSelector.ui.Q = NucleusSelector.nuc.state.Q;
      NucleusSelector.ui.T = NucleusSelector.nuc.state.T.toFixed(4);
      NucleusSelector.ui.Brho = NucleusSelector.nuc.state.Brho.toFixed(4);
      NucleusSelector.ui.Beta   = NucleusSelector.nuc.state.Beta.toFixed(6);
      NucleusSelector.ui.Gamma   = NucleusSelector.nuc.state.Gamma.toFixed(5);
      NucleusSelector.ui.P   = NucleusSelector.nuc.state.P.toFixed(3);
      NucleusSelector.ui.Nth   = NucleusSelector.nuc.state.Nth.toFixed(4);
      if(isNaN(NucleusSelector.ui.Nth)){
        NucleusSelector.ui.Nth = "";
      }
      App.clear_cache();
  };

NucleusSelector.ui.changesymbol = function(sym){
    NucleusSelector.ui.Symbol = sym;
    if(NucleusSelector.nuc.SetNucleusSymbol(sym)){
        NucleusSelector.ui.uiupdate();
        }
    };


NucleusSelector.ui.changeA = function(val){
    NucleusSelector.ui.A = val;
    var num = parseFloat(val);
    if(!isNaN(num) && NucleusSelector.nuc.Set.A(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.A = val;
        }
    };

NucleusSelector.ui.changeZ = function(val){
    NucleusSelector.ui.Z = val;
    var num = parseFloat(val);
    if(!isNaN(num) && NucleusSelector.nuc.Set.Z(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.Z = val;
        }
    };

NucleusSelector.ui.changeQ = function(val){
    NucleusSelector.ui.Q = val;
    var num = parseFloat(val);
    if(!isNaN(num) && NucleusSelector.nuc.Set.Q(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.Q = val;
        }
    };

NucleusSelector.ui.changeT = function(val){
    NucleusSelector.ui.T = val;
    var num = parseFloat(val);
    if(isNaN(num))return;
    if(NucleusSelector.nuc.Set.T(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.T = val;
        }
    };

NucleusSelector.ui.changeBeta = function(val){
    NucleusSelector.ui.Beta = val;
    var num = parseFloat(val);
    if(isNaN(num))return;
    if(NucleusSelector.nuc.Set.Beta(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.Beta = val;
        }
    };
    
NucleusSelector.ui.changeGamma = function(val){
    NucleusSelector.ui.Gamma = val;
    var num = parseFloat(val);
    if(isNaN(num))return;
    if(NucleusSelector.nuc.Set.Gamma(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.Gamma = val;
        }
    };

NucleusSelector.ui.changeBrho = function(val){
    NucleusSelector.ui.Brho = val;
    var num = parseFloat(val);
    if(isNaN(num))return;
    if(NucleusSelector.nuc.Set.Brho(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.Brho = val;
        }
    };
    
NucleusSelector.ui.changeP = function(val){
    NucleusSelector.ui.P = val;
    var num = parseFloat(val);
    if(isNaN(num))return;
    if(NucleusSelector.nuc.Set.P(num)){
        NucleusSelector.ui.uiupdate();
        NucleusSelector.ui.P = val;
        }
    };

NucleusSelector.ui.changeNth = function(val){
        NucleusSelector.ui.Nth = val;
        var num = parseFloat(val);
        if(isNaN(num))return;
        if(NucleusSelector.nuc.Set.Nth(num)){
            NucleusSelector.ui.uiupdate();
            NucleusSelector.ui.Nth = val;
            }
        };

NucleusSelector.ui.done = function(){
    if(NucleusSelector.nuc.state.T>Catima.energy_max){
          alert("Maximal allowed energy is "+Catima.energy_max+" MeV/u");
          NucleusSelector.nuc.Set.T(Catima.energy_max);
      }
    if(NucleusSelector.nuc.state.T<Catima.energy_min){
        alert("Set Energy more than "+Catima.energy_min+" MeV/u");
        }

        App.Save();
        m.route.set("/");    
    };

function form_element(args){
    return m("div",{class:styles.form_element},[
        m("label",{class:styles.label,for:args.id},args.label),
        m("input",{
            class:"db",
            id:args.id||"_id"+args.label,
            value:args.value, 
            type:args.type||"text",
            placeholder:args.placeholder||"",
            style:args.style||"",
            oninput:args.oninput})
        ]);
}

NucleusSelector.view = function(vnode){
  var cell = function(e){
      return m("div",{class:styles.div_cell},e);
  };
 
  var ui = vnode.state.ui;
  var nucinfo = NucData.GetNucData(NucleusSelector.nuc.state.A,NucleusSelector.nuc.state.Z);
  var nucs = NucData.GetNucS(NucleusSelector.nuc.state.A,NucleusSelector.nuc.state.Z);
  
  return m("div",
    [
    m("h2","Projectile"),
    m("div",{class:styles.div_row},[
        cell(form_element({label:"Symbol",placeholder:"ie 12C",style:"width:7em",value:ui.Symbol,oninput:m.withAttr("value",ui.changesymbol,ui)})),
        cell(form_element({label:"A",style:"width:7em",value:ui.A,oninput:m.withAttr("value",ui.changeA,ui)})),
        cell(form_element({label:"Z",style:"width:7em",value:ui.Z,oninput:m.withAttr("value",ui.changeZ,ui)})),
        cell(form_element({label:"Q",style:"width:7em",value:ui.Q,oninput:m.withAttr("value",ui.changeQ,ui)}))
    ]), // end of 1st row
    m("div",{class:styles.div_row},[
        cell(form_element({label:"Energy [MeV/u]",style:"width:7em",value:ui.T,oninput:m.withAttr("value",ui.changeT,ui)})),
        cell(form_element({label:"Brho [Tm]",style:"width:7em",value:ui.Brho,oninput:m.withAttr("value",ui.changeBrho,ui)})),
        cell(form_element({label:m.trust("&#946;"),style:"width:7em",value:ui.Beta,oninput:m.withAttr("value",ui.changeBeta,ui)})),
        cell(form_element({label:m.trust("&#947;"),style:"width:7em",value:ui.Gamma,oninput:m.withAttr("value",ui.changeGamma,ui)}))
    ]), //end of 2nd row
    m("div",{class:styles.div_row},[
        cell(form_element({label:m.trust("p [MeV/c]"),style:"width:7em",value:ui.P,oninput:m.withAttr("value",ui.changeP,ui)})),
        cell(form_element({label:m.trust("n<sub>th</sub>"),style:"width:7em",value:ui.Nth,oninput:m.withAttr("value",ui.changeNth,ui)}))
    ]), //end of 3rd row
    m("div",{class:styles.div_row},[
        m("ul",{class:styles.ul},[
                m("li",{class:styles.li},"mA = ",NucleusSelector.nuc.state.mA.toFixed(5)+" u"),
                m("li",{class:styles.li},"A/Q = ",NucleusSelector.nuc.state.AoQ.toFixed(5)),
                (nucinfo.hasOwnProperty("T"))?m("li",{class:styles.li},"half life = ",nucinfo.T):null,
                (nucs.hasOwnProperty("Sn"))?m("li",{class:styles.li},"Sn = ",nucs.Sn.toFixed(3)+" MeV"):null,
                (nucs.hasOwnProperty("Sp"))?m("li",{class:styles.li},"Sp = ",nucs.Sp.toFixed(3)+" MeV"):null,
                (nucs.hasOwnProperty("S2n"))?m("li",{class:styles.li},"S2n = ",nucs.S2n.toFixed(3)+" MeV"):null,
                (nucs.hasOwnProperty("S2p"))?m("li",{class:styles.li},"S2p = ",nucs.S2p.toFixed(3)+" MeV"):null,
                (nucinfo.hasOwnProperty("M"))?null:m("li",{class:styles.li},"warning: Nucleus was not found in the AME16 database")
        ])
    ]), //3rd row
    m("div",{class:styles.div_row},
        
        button("Done",ui.done, styles.button_full)
                )
    ]);
};

var NucleusInfo = {};
NucleusInfo.view = function(vnode){
  var state = vnode.attrs.nucleus.state;
  return m("div",{class:styles.div_column},[
    
        m("span",{class:styles.h3},m.trust(state.Symbol+"<sup>"+state.Q+"+</sup>")), 
        m("p",{class:"f5"},[
            "T = ",(state.T<1e-3)?text_error(state.T.toPrecision(6)):state.T.toPrecision(6)," MeV/u",m("br"),
            m.trust("B&#961= "),state.Brho.toPrecision(6)," Tm",m("br"),
            "mA = ",state.mA.toFixed(5)," u",m("br"),
            "A/Q = ",state.AoQ.toFixed(4),m("br"),
        ]),
        button_route("Change","/nucchange"),

   ]//row
    );
};

export {NucleusSelector, NucleusInfo};
