import {button, button_route} from "./utils.js";
import {config_presets} from "./catima_config.js";
import {styles} from "./styles.js";
import {App} from "./app.js";

var ConfigComponent = {};

ConfigComponent.view = function(){
    return  m("div",{class:styles.div_column},[
        m("div",{class:styles.form_element},[

            m("h1",{class:styles.heading_medium},"Presets:"),
            config_presets.map(function(item){
                if(App.state.config.is_same(item.config))
                    return button(item.name);
                else   
                    return m("a",
                            {class:styles.button_inverted,
                            onclick:function(){
                                App.state.config.set(item.config);
                                App.clear_cache();
                                App.Save();
                                }
                            }, 
                            item.name);
            })
        ]),
        //, //end of 1st row
        //m("div",{class:styles.div_row},[
        //    m("div",{class:styles.div_cell},[
        //        m("h1",{class:styles.heading_medium},"Mean Charge:"),
        //        catima_z_eff.map(function(item){
        //            return m("label",{class:"c-field c-field--choice"},
        //            [m("input",{type:"radio",name:"zeff", checked:(App.state.config.z_effective===item[1]),value:item[1]}),item[0]]
        //        );
        //        
        //        })
         //   ]) // end 2nd row, 1st columne
        //]) // end of 2nd row
        m("div",{class:styles.form_element},button_route("Done","/"))
    ]
    );
};

export {ConfigComponent};
