//// initialization //////
Raven.context(function(){
var main_element = document.getElementById('loading');
main_element.innerHTML = loader("loading");
var ss = window.location.search;
var idx = ss.indexOf("?c=");
if(ss.length>3 && idx>-1){
    var len = ss.length;
    if(ss.charAt(len-1)=="/"){
        len = len - 1;
    }
    var uid = ss.substring(idx+3, len);
    App.load_config(uid);
}
else{
    var stored = StorageAPI.load("state");
    App.Load(stored);
}

m.route(document.getElementById('NS'),"/",{
  "/":MainPage ,
  "/nucchange":NucChangePage,
  "/results":ResultsPage,
  "/plot":PlotPage,
  "/materialplot":MaterialPlotPage,
  "/config":ConfigPage,
  "/epax":EpaxPage
});


});
