import {App} from "./app.js";
import {spline} from "../vendor/spline";

var Catima = {};

Catima.energy_min = 1e-3;
Catima.energy_max = 1e5;
Catima.epsilon = 1e-9;


Catima.url = "https://isotopea.com/api/catima/";
//Catima.url = "http://127.0.0.1:5000/";

Catima.check_material_duplicate = function(arr,val){
    return  arr.some(function(el){
        return (el.A==val.A && el.Z==val.Z && el.mass==val.mass);
    });
};

Catima.prepare = function(unique,valid){
    var input = {};
    var nuc = App.projectile.state;
    var matter = App.MatterEditor.vm.layers;
    if(matter.length<1 || nuc.T<1e-3)return {};
    input.projectile={ 
                        "mA":nuc.mA,
                        "A":nuc.A,
                        "Z":nuc.Z,
                        "Q":nuc.Q,
                        "T":nuc.T
                    };
    input.matter = [];
    for(var i=0;i<matter.length;i++){
        //var v = matter[i].toArray();
        var v = matter[i].toObject();
        var b = false;
        if(unique){
            b = Catima.check_material_duplicate(input.matter,v);
        }

        if(valid){
            if(matter[i].rho > 0 && matter[i].th > 0){
            if(!unique || !b)
                input.matter.push(v);
            }
        }
        else{
            if(!unique || !b)
                input.matter.push(v);
        }
      }
  
      if(App.state.config){
          input.config = App.state.config;
      }
    return input;
};

Catima.prepare_one = function(index){
    var input = {};
    var nuc = App.projectile.state;
    var matter = App.MatterEditor.vm.layers;
    if(matter.length<1 || nuc.T<1e-3)return {};

    input.projectile={ 
        "mA":nuc.mA,
        "A":nuc.A,
        "Z":nuc.Z,
        "Q":nuc.Q,
        "T":nuc.T
    };
    input.matter = [];
    if(index>=matter.length)return {};
    //var v = matter[index].toArray();    
    var v = matter[index].toObject();
    if(matter[index].rho > 0){
        input.matter.push(v);
        }
    
    if(App.state.config){
        input.config = App.state.config;
        }
    return input;
};

Catima.check_cache = function(matter){
    var res = [];
    /*  this was older version where matter layer was as array
    for(var i=0;i<Catima.data.length;i++){
        var e = Catima.data[i];
        if(e.Zm>200 && e.Zm == matter[1]){
            if(e.I>0 && (matter[6]!=e.I))continue;
            res.push(e);
            break;
            }
        if(e.Zm == matter[1] && e.Am == matter[0] && Math.abs(e.M - matter[5])<Catima.epsilon){
            if(e.I>0 && (matter[6]!=e.I))continue;
            res.push(e);
            break;
            }
        
        }*/

        for(var i=0;i<Catima.data.length;i++){
            var e = Catima.data[i];
            if(e.Zm>200 && e.Zm == matter.Z){
                if(e.I>0 && (matter.I!=e.I))continue;
                res.push(e);
                break;
                }
            if(e.Zm == matter.Z && e.Am == matter.A && Math.abs(e.M - matter.mass)<Catima.epsilon){
                if(e.I>0 && (matter.I!=e.I))continue;
                res.push(e);
                break;
                }
            
            }

    return res;
};

Catima.data = [];
Catima.clear = function(){
    Catima.data = [];
};

Catima.check_input = function(input){
    if(!input.projectile && input.projectile.T>0){
        return false;
    }
    if(!input.matter || !Array.isArray(input.matter)){
        return false;
    }
    if(input.matter.length<1){
        return false;
    }
    return true;
};

// calculate layers
Catima.calculate = function(input){
    if(!Catima.check_input(input)){
        App.report("wrong input",input);
        return Promise.resolve({});
    }
    return m.request({method:"POST",url:Catima.url+"calculate", data:input, background:true}).then(
        function(res){
            return res;
        },
        function(){
          App.report("Catima could not retrieve data");
          return {"error":"Failed to retrieve data"};
        }
        );
};

// load catima data from server if not in cache already, if loading from server Promise is returned.
Catima.get_data = function(input){
    if(!Catima.check_input(input)){
        App.report("wrong input",input);
        return Promise.resolve([]);
    }

    if(input.matter.length == 1){
        var cache = Catima.check_cache(input.matter[0]);
        if(cache.length == 1){
            return Promise.resolve(cache);
        } 
    }

    return m.request({method:"POST",url:Catima.url+"data", data:input, background:false}).then(
        function(res){
            if(!res.data){
                return []; 
            }
            Catima.data = [];
            for(var i=0;i<res.data.length;i++){
                var entry = {};
                entry.energy = new Float32Array(res.energy);
                entry.projectile = res.projectile;
                entry.dEdx = new Float32Array(res.data[i].dEdx);
                entry.range = new Float32Array(res.data[i].range);
                var zm = parseInt(res.data[i].Zm);
                var am = parseFloat(res.data[i].Am);
                entry.Am = am;
                entry.Zm = zm;
                entry.M = res.data[i].M;
                entry.I = (res.data[i].I)?res.data[i].I:0;
                Catima.data.push(entry);
            }
            return Catima.data;
        },
        function(){
          return [];
        }
        );
};

Catima.energy_out = function(T, thickness, dedx_spline, range_spline){
    var range = range_spline.at(T);
    var dedx = dedx_spline.at(T);
    if(range<thickness){
        return 0.0;
        }
    var e = T - (thickness*dedx);
    if(e<1e-3)return 0.0;
    for(var i=0;i<100;i++){
        var r = range - range_spline.at(e)-thickness;
        if(Math.abs(r)<1e-4){
            return e;
        }
        var step = -r*dedx;
        e = e - step;
        if(e<1e-3)return 0.0;
        dedx = dedx_spline.at(e);
    }
    return -1;
};

Catima.Eout_vs_thickness = function(data, T){
    var sdedx = new spline(data.energy,data.dEdx);
    var srange = new spline(data.energy,data.range);
    var range = srange.at(T);
    
    //var num = range/0.001;
    var num = 100;
    if(num>1000)num=1000;
    if(num<10)num=10;
    
    var step = T/(num);
    if(step<1e-3)step=1e-3;

    var thicknesses = new Float32Array(num);
    var eout = new Float32Array(num);
    var Eloss = new Float32Array(num);
    var dedxi = new Float32Array(num);
    
    var e = T;
    thicknesses[0] = 0.0;
    eout[0] = e;
    Eloss[0] = 0.0;
    dedxi[0] = sdedx.at(T);
    for(var i = 1; i<num; i++){
        e = e - step;
        eout[i] = e;
        var rdif = range-srange.at(e);
        thicknesses[i] = rdif;
        var edif = (T - e);
        Eloss[i] = edif*data.projectile.A;
        dedxi[i] = sdedx.at(e);
    }
    return [thicknesses, eout, Eloss,dedxi];
};

Catima.get_energy_out = function(){
    var matter = App.MatterEditor.vm.layers;
    if(matter.length==1 && matter[0].th==0.0){
        return Promise.resolve([App.projectile.state.T]);
        }
    return Catima.get_data(Catima.prepare()).then(function(){
    var eouts = [];
    var e = App.projectile.state.T;
    for(var i=0;i<matter.length;i++){
        var data = Catima.check_cache(matter[i]);
        if(data.length!=1){
            console.log("cache not found?, length = "+data.length);
            App.report("Cache not found? "+data.length);
            return[];
            }
        var res = Catima.Eout_vs_thickness(data[0],e);
        var eout_vs_th = new spline(res[0],res[1]);
        e = eout_vs_th.at(matter[i].th*0.001);
        eouts.push(e);
        }
    return eouts;
    });
};


Catima.load_config = function(id){
    return m.request({method:"POST", url:Catima.url+"load", data:{"id":id}});
};

export {Catima};
