var catima_z_eff = [
    ["none",0],
    ["Pierce-Blann",1],
    ["Anthony-Landorf",2],
    ["Hubert",3],
    ["Winger",4],
    ["Schiwietz",5],
    ["Global",6],
    ["Atima v1.4",7]
];

var catima_omega = {
   "atima1.3":0,
   "Bohr":1
};

export var config_atima13 = {"z_effective":1, "dedx_straggling":0};
export var config_atima14 = {"z_effective":7, "dedx_straggling":0};

export var config_presets = [
    {"name":"atima v1.3","config":config_atima13},
    {"name":"atima v1.4","config":config_atima14}
];


export function catima_config(init){
    this.z_effective = config_atima13.z_effective;
    this.dedx_straggling = config_atima13.dedx_straggling;
    if(init){
        if(init.z_effective)
            this.z_effective = init.z_effective;
        if(init.dedx_straggling)
            this.dedx_straggling = init.dedx_straggling;
    }

}

catima_config.prototype.is_same = function(other){
    if(this.z_effective == other.z_effective && this.dedx_straggling == other.dedx_straggling)
        return true;
    else
        return false;
};

catima_config.prototype.set = function(other){
    this.z_effective = other.z_effective;
    this.dedx_straggling = other.dedx_straggling;
};
